var _this;
//登录的用户的增加用户入库的api
import {register} from '../../static/utils/api.js';

//封装好的登录的函数
const login = require('../../static/utils/utils.js').Login;
const Login = new login();

export default {
	data() {
		return {
			canIUse:wx.canIUse('button.open-type.getUserInfo'),
			trueName:"",
			disable:true
		}
	},
	
	//登录之前就查看本地是否存在用户证明授权登录过直接跳转到申请表
	mounted() {
	   
	},
	 methods:{
		 
	  //输入这是的姓名	 
	  inputTrueName:function(e){
		  let trueName = e.detail.value;
		  let reg = /^[\u4e00-\u9fa5]+$/i;  //正则表达式匹配中文
		  if(reg.test(trueName)&&trueName.length<=4&&trueName.length>=2){
			  this.trueName = trueName;
			  this.disable = false;
		  }else{
			  this.disable = true;
			  uni.showToast({
			  	title:"输入有误",
				duration:1000,
				icon:"none"
			  })
		  }
	  },
		 
		   
	  bindGetUserInfo:function(){
		  let openId;
		  let sessionKey;
		  let trueName = this.trueName;
		  uni.showLoading({
			  title:"登录中",
		  	  success:()=>{
			   Login.getCode()
			   .then(data=>{
			   		return Login.getOpenId(data)
			   })
			   .then(data=>{
			   	   openId = JSON.parse(data.data.data).openid;
				   sessionKey = JSON.parse(data.data.data).session_key;
			   	   return Login.getUserInfo()
			   })
			   .then(data=>{
			   	   let userInfo = {
					    ...data.userInfo, 
			   			openId,
						sessionKey,
						trueName
			   		};
			   	    return Login.insertUser(userInfo,register,"POST")
			   })
			   .then(data=>{
				   uni.hideLoading();
				   console.log("返回的用户的信息",data)
				   uni.setStorage({
				   	 key:'userInfo',
					 data:data.data,
					 success:()=>{
						 if(data.data.isRoot){  //为超级用户直接跳转people
							 uni.redirectTo({
							 	url:"../people/people"
							 })
						 }else{
							 uni.redirectTo({
								url:'../apply/apply'
							}) 
						 }
					 },
					 fail:()=> {
					   	uni.showToast({
					   		title:"网络连接错误",
							duration:1000,
							icon:"loading"
					   	})
		              }
				    })		
				})	     
			   .catch(error=>{
			   	  console.log(error)
			   })	
			}
		  })
	}
  }
}
